import React, { LegacyRef, MutableRefObject } from 'react';
import { render } from 'react-dom';
import {
	Stage,
	Layer,
	Star,
	Text,
	Label,
	Tag,
	Image as KonvaImage,
} from 'react-konva';
import Konva from 'konva';
import * as R from 'ramda';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import useImage from 'use-image';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import { Container, SelectChangeEvent, TextField } from '@mui/material';
import MuiInput from '@mui/material/Input';
import { useWindowSize } from 'react-use';
import { ColorResult, TwitterPicker } from 'react-color';

import Canvas, { P_Canvas } from './Canvas';
import TextForm from './TextForm';
import FontFamilySelector from './FontFamilySelector';
import {
	FONT_COLORS,
	FONT_FAMILIES,
	FONT_SIZE_LIST,
	FONT_STROKE_COLORS,
	LOCAL_IMAGE_INPUT_ID,
	t_fontStrokeColor,
} from './const';
import FontSizeSelector from './FontSizeSelector';
import FontStrokeColorSelector from './FontStrokeColorSelector';
import BgImageSelectTabs from './BgImageSelectTabs';

const TESTTEXTS = '';
const TESTTEXTL = ` - 108
- - 
- - 
- - 
- -   
- - 
- -  
- - 
- -    `;

const URL_CIRCLE = 'https://i.imgur.com/EpFWMQl.png';
const URL_MIDO = 'https://i.imgur.com/cNvWq7Z.png';

const URL = URL_MIDO;

export default function Product() {
	const [state_text, set_text] = React.useState<string>(TESTTEXTL);
	const [state_currentTabNumber, set_currentTabNumber] =
		React.useState<string>('2'); //1~3
	const [state_localFileDataBase, set_localFileDataBase] =
		React.useState<string>();
	const installedFontFamilies: string[] = React.useMemo(
		() => flatJoin(',', filterInstalledFont(FONT_FAMILIES)),
		[],
	);
	const [state_fontFamily, set_fontFamily] = React.useState<string>(
		installedFontFamilies[0],
	);
	const [state_fontSize, set_fontSize] = React.useState<number>(28);
	const [state_fontColor, set_fontColor] = React.useState<string>(
		FONT_COLORS[0],
	);
	const fontStrokeColorNames: string[] = React.useMemo(
		() => FONT_STROKE_COLORS.map((e) => e.name),
		[],
	);
	const [state_fontStrokeColorName, set_fontStrokeColorName] =
		React.useState<string>(fontStrokeColorNames[0]);
	const [state_lineHeight, set_lineHeight] = React.useState<number>(1.5);
	const [state_origImageW, set_origImageW] = React.useState<number>(100);
	const [state_origImageH, set_origImageH] = React.useState<number>(100);
	const [state_maxStageW, set_maxStageW] = React.useState<number | undefined>();
	const ref_stage = React.useRef<Konva.Stage>(null);
	const ref_image = React.useRef<Konva.Image>(null);

	React.useEffect(() => {
		(async () => {
			await setOrigImageSize();
		})();
	}, []);
	React.useEffect(() => {
		const r = document.getElementById('textFormContainer');
		r && set_maxStageW(r.offsetWidth);
	}, []);

	React.useEffect(() => {
		console.log({
			tabbbb: document.getElementById(LOCAL_IMAGE_INPUT_ID),
			state: state_currentTabNumber,
		});
		document.getElementById(LOCAL_IMAGE_INPUT_ID)?.addEventListener(
			'change',
			function (e) {
				const target = e.currentTarget as HTMLInputElement;
				if (!target || !target.files) {
					return;
				}
				// 
				const fileData = target.files[0];

				// 
				if (!fileData.type.match('image.*')) {
					alert('');
					return;
				}

				// FileReader
				const reader = new FileReader();
				// 
				reader.onload = function () {
					const base = reader.result;
					if (typeof base !== 'string') {
						return;
					}

					set_localFileDataBase(base);

					console.log({
						filedata_loaded: fileData,
						base: base,
					});
				};
				// 
				reader.readAsDataURL(fileData);
			},
			false,
		);
	}, [state_currentTabNumber]);

	const onImageSelectTabChange = React.useCallback(
		(event: React.SyntheticEvent, newValue: string) =>
			set_currentTabNumber(newValue),
		[],
	);

	const onFontFamilyChange = React.useCallback(
		(event: SelectChangeEvent) => set_fontFamily(event.target.value),
		[],
	);

	const setOrigImageSize = async () => {
		try {
			const res = await loadImage(URL);
			set_origImageW(res.width);
			set_origImageH(res.height);
			console.log(['hyou!', res.width, res.height]);
		} catch (e) {
			console.log('hyou! onload error', e);
		}
	};

	const onTextChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_text(e.target.value),
		[],
	);

	const onFonSizeChange = React.useCallback(
		(event: SelectChangeEvent) => set_fontSize(Number(event.target.value)),
		[],
	);

	const onFonColorChange = React.useCallback(
		(e: ColorResult) => set_fontColor(e.hex),
		[],
	);

	const onFontStrokeColorChange = React.useCallback(
		(event: SelectChangeEvent) => set_fontStrokeColorName(event.target.value),
		[],
	);

	/**
	 * 
	 */
	const fontStrokeColorValue = React.useMemo(
		() =>
			R.pipe(
				R.find<t_fontStrokeColor>(R.propEq('name', state_fontStrokeColorName)),
				R.defaultTo(FONT_STROKE_COLORS[0]),
				R.prop('value'),
			)(FONT_STROKE_COLORS),
		[state_fontStrokeColorName],
	);

	const onLineHeightChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_lineHeight(Number(e.target.value)),
		[],
	);

	const onMaxStageWChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_maxStageW(Number(e.target.value)),
		[],
	);

	const save = () => {
		const uri = ref_stage.current?.toDataURL();
		uri && downloadURI(uri, 'stage.png');
	};
	const onTest = () => {
		ref_image.current?.moveToTop();
	};

	return (
		<div>
			<Stack spacing={2}>
				<Container maxWidth='sm'>
					<Stack spacing={2}>
						<div id='textFormContainer'>
							<TextForm value={state_text} onChange={onTextChange} />
						</div>
						<Demo />
						<Button variant='contained' onClick={onTest}>
							test
						</Button>
						<Button variant='contained' onClick={save}>
							save
						</Button>
						<BgImageSelectTabs
							localImageInputId={LOCAL_IMAGE_INPUT_ID}
							currentTabNumber={state_currentTabNumber}
							onChange={onImageSelectTabChange}
						/>
						<FontFamilySelector
							fontFamilies={installedFontFamilies}
							value={state_fontFamily}
							onChange={onFontFamilyChange}
						/>
						<FontSizeSelector
							sizeList={FONT_SIZE_LIST}
							value={state_fontSize}
							onChange={onFonSizeChange}
						/>
						<div
							style={{
								display: 'flex',
								flexDirection: 'row',
								justifyContent: 'flex-start',
								alignItems: 'flex-end',
							}}
						>
							<p
								style={{
									marginLeft: 0,
									marginTop: 0,
									marginBottom: 0,
									marginRight: 8,
									padding: 0,
									color: 'rgba(0, 0, 0, 0.6)',
								}}
							>
								:{' '}
							</p>
							<div style={{ flexGrow: 2 }}>
								<TwitterPicker
									onChange={onFonColorChange}
									color={state_fontColor}
									colors={FONT_COLORS}
									triangle={'hide'} //
									width={'100%'}
								/>
							</div>
						</div>
						<FontStrokeColorSelector
							strokeColorNames={fontStrokeColorNames}
							onChange={onFontStrokeColorChange}
							value={state_fontStrokeColorName}
						/>
						<TextField
							id='line-height'
							label='line height'
							type='number'
							InputLabelProps={{
								shrink: true,
							}}
							size='small'
							defaultValue={state_lineHeight}
							onChange={onLineHeightChange}
							inputProps={{
								step: 0.1,
								min: 0,
								max: 99999,
								type: 'number',
							}}
						/>
						{state_maxStageW !== undefined && (
							<TextField
								id='max-width'
								label='max width'
								type='number'
								InputLabelProps={{
									shrink: true,
								}}
								size='small'
								defaultValue={state_maxStageW}
								onChange={onMaxStageWChange}
							/>
						)}
					</Stack>
				</Container>
				{state_maxStageW !== undefined && (
					<div
						style={{
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'center',
						}}
					>
						<Canvas
							fontFamily={state_fontFamily}
							fontSize={state_fontSize}
							fontColor={state_fontColor}
							fontStrokeColor={fontStrokeColorValue}
							lineHeight={state_lineHeight}
							maxStageWidth={state_maxStageW}
							text={state_text}
							imageUrl={state_localFileDataBase ? state_localFileDataBase : ''}
							origImageH={state_origImageH}
							origImageW={state_origImageW}
							ref_stage={ref_stage}
							ref_image={ref_image}
						/>
					</div>
				)}
			</Stack>
		</div>
	);
}

const Demo = () => {
	const { width, height } = useWindowSize();

	return (
		<div>
			<div>width: {width}</div>
			<div>height: {height}</div>
		</div>
	);
};
const loadImage = (src: string): Promise<HTMLImageElement> => {
	return new Promise((resolve, reject) => {
		const img = new Image();
		img.onload = () => resolve(img);
		img.onerror = (e) => reject(e);
		img.src = src;
	});
};

function downloadURI(uri: string, name: string) {
	var link = document.createElement('a');
	link.download = name;
	link.href = uri;
	document.body.appendChild(link);
	link.click();
	document.body.removeChild(link);
}

/**
 * fontList1filter
 * @param fontList
 * @returns
 */
const filterInstalledFont = (fontList: string[][]) =>
	R.filter(R.any(isFontInstalled), fontList);

const isFontInstalled = (fontName: string) =>
	document.fonts.check('bold 12px ' + fontName);

const flatJoin = (separator: string, e: any[][]) =>
	e.map((e) => e.join(separator));
